.timer {
  display: flex;
  justify-content: center;
  background-color: #f0f2f1;
  width: 600px;
  margin: 40px auto;
}

.field {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;

  & .value {
    background-color: #353635;
    color: #f8f5f5;
    font-size: 55px;
    font-weight: 700;
    letter-spacing: 0.06em;
    text-align: center;
    width: 80px;
    padding: auto 5px;
    margin-bottom: 10px;
  }

  & .label {
    font-size: 15px;
    text-transform: uppercase;
  }
}
